
import { Options, Vue } from 'vue-class-component'
import infoImg from '@/assets/popup_info.gif'
import libImg from '@/assets/popup_lib.gif'
import learnImg from '@/assets/popup_learn.gif'
import mailImg from '@/assets/popup_mail.gif'

@Options({
  props: {
    name: String
  }
})

export default class Info extends Vue {
  name!: string
  hour!: string
  minute!: string
  second!: string
  startDate!: Date
  usageNumber!: number // record as bytes
  buttonColor!: string
  /* eslint-disable-next-line */
  links!: Array<{name : string, link : string, img : any}>

  data () {
    return {
      hour: '00',
      minute: '00',
      second: '00',
      startDate: new Date(),
      usageNumber: 10000000000,
      buttonColor: '#c0bdcc',
      links: [
        {
          name: 'Info',
          img: infoImg,
          link: 'http://info.tsinghua.edu.cn'
        },
        {
          name: 'Lib',
          img: libImg,
          link: 'https://lib.tsinghua.edu.cn'
        },
        {
          name: 'Learn',
          img: learnImg,
          link: 'https://learn.tsinghua.edu.cn'
        },
        {
          name: 'Mail',
          img: mailImg,
          link: 'https://mails.tsinghua.edu.cn'
        }
      ]
    }
  }

  mounted () {
    this.startDate = new Date()
    setInterval(() => {
      this.updateTime()
    }, 500)
    console.log('Info mounted')
  }

  public updateTime () : void {
    const now = new Date()
    const diff = now.getTime() - this.startDate.getTime()
    const hour = Math.floor(diff / (1000 * 60 * 60))
    const minute = Math.floor(diff / (1000 * 60)) % 60
    const second = Math.floor(diff / 1000) % 60
    this.hour = hour.toString().padStart(2, '0')
    this.minute = minute.toString().padStart(2, '0')
    this.second = second.toString().padStart(2, '0')
  }

  public clickDisconnect () : void {
    alert('您已断开连接')
  }

  get usageNumberLength () : string {
    const percentage = Math.ceil(Math.min(this.usageNumber / (125 * 1024 * 1024 * 1024), 1) * 100)
    return `${percentage}%`
  }

  get usageNumberText () : string {
    // as bytes
    if (this.usageNumber < 1024) {
      return `${this.usageNumber}B`
    } else if (this.usageNumber < 1024 * 1024) {
      return `${(this.usageNumber / 1024).toFixed(2)}KB`
    } else if (this.usageNumber < 1024 * 1024 * 1024) {
      return `${(this.usageNumber / (1024 * 1024)).toFixed(2)}MB`
    } else {
      return `${(this.usageNumber / (1024 * 1024 * 1024)).toFixed(2)}GB`
    }
  }
}
